@import "reset.css";
@font-face {
  font-family: "Cafe24Ohsquare";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/Cafe24Ohsquare.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF Light";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF Light.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 62.5%;
}

body {
  /*  */
  font-family: "NEXON Lv1 Gothic OTF", "Cafe24Ohsquare", system-ui, Roboto,
    "Helvetica Neue", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic",
    sans-serif;
  background-color: rgb(221, 221, 221);
}

li {
  list-style: none;
}

a {
  text-decoration: none !important;
}

.divider {
  border-bottom: 1px solid gray;
}

.layout {
  margin-left: max(20rem, min(30rem, 30%));
}

.type::after {
  content: "|";
  animation: cursor 1.1s infinite step-start;
}

@keyframes cursor {
  50% {
    opacity: 0;
  }
}

.section {
  min-width: 50rem;
  padding: 10% calc(4% + 200px) 10% 4%;
  background-color: rgb(221, 221, 221);
}

@media screen and (max-width: 800px) {
  .section {
    padding: max(10%, 2rem) max(4%, 2rem);
  }
}
